import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Rx';

// Import RxJs required methods
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import {User} from '../../models/users.interface'

/*
  Generated class for the ApiProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ApiProvider {
  apiUrl = 'http://localhost:8080/';
  authData: any;
  private dataObserver;
  private users: User[];
  private uAuth: User[];
  

  constructor(private http: HttpClient) {
    console.log('Hello ApiProvider Provider');
  }

  getUsers() {
    return new Promise(resolve => {
      this.http.get(this.apiUrl+'api/users').subscribe(data => {
        resolve(data);
      }, err => {
        console.log(err);
      });
    });
  }

  getUser(user){
    return this.http.get(this.apiUrl+'api/me/?name='+localStorage.getItem('name')+'&token='+localStorage.getItem('token'))
    .map(res => res)
    .toPromise();
  }

  auth(user): Promise<any>{
    return this.http.post(this.apiUrl+'api/authenticate', 
      "name="+user.name+"&password="+user.password,
      { headers: { 'Content-Type': 'application/x-www-form-urlencoded' }}
    )
    .map(res => res)
    .toPromise();
  }

  register(user): Promise<any>{
    return this.http.post(this.apiUrl+'register', 
      "name="+user.name+"&password="+user.password+"&email="+user.email,
      { headers: { 'Content-Type': 'application/x-www-form-urlencoded' }}
    )
    .map(res => res)
    .toPromise();
  }

}



