import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, App } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { ApiProvider } from '../../providers/api/api';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  user: any;
  userData: any;

  constructor(public app: App, public navCtrl: NavController, public navParams: NavParams, public http: HttpClient, public apiProvider: ApiProvider) {
    this.user = {
      name: '',
      password:'',
      address:'',
      state:'',
      city:'',
      description:'',
      email:''
    };
    this.getUserData();
    console.log(localStorage.getItem('token'));
  }

  async getUserData(){
    
    this.userData = await this.apiProvider.getUser(this.user);
    this.user.name = this.userData.data.name;
    this.user.email = this.userData.data.email;
    this.user.password = this.userData.data.password;
    this.user.address = this.userData.data.address;
    this.user.state = this.userData.data.state;
    this.user.city = this.userData.data.city;
    this.user.description = this.userData.data.description;
    this.user.email = this.userData.data.email;
    
  }

  logout(){
    const root = this.app.getRootNav();
    root.popToRoot();
  }

}



