import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, App } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { ApiProvider } from '../../providers/api/api';
import { TabsPage} from '../tabs/tabs';

/**
 * Generated class for the Signup page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class Signup {
  user: any;
  userRegistered: any;
  auth: any;
  constructor(public app: App, public navCtrl: NavController, public navParams: NavParams, public http: HttpClient, public apiProvider: ApiProvider) {
    this.user = {
      name:'',
      password:'',
      email:'',
      address:'',
      state:'',
      city:'',
      description:'' 
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Signup');
  }

  goBack(){
    const root = this.app.getRootNav();
    root.popToRoot();
  }

  async signup(){
    this.userRegistered = await this.apiProvider.register(this.user);
    console.log('teste', this.userRegistered);
    if(this.userRegistered.success === true){
      localStorage.setItem('token', this.userRegistered.token);
      this.navCtrl.push(TabsPage);
    }
    else{
      console.log('Erro ao registrar');
    }
  }

}



