import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, App } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { ApiProvider } from '../../providers/api/api';
import { TabsPage} from '../tabs/tabs';

/**
 * Generated class for the Login page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class Login {
  user: any;
  auth: any;
  constructor(public app: App, public navCtrl: NavController, public navParams: NavParams, public http: HttpClient, public apiProvider: ApiProvider) {
    apiProvider.getUsers();
    this.user = {
      name:'',
      password:''
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Login');
  }

    goBack(){
    const root = this.app.getRootNav();
    root.popToRoot();
  }

  async login(){
    this.auth = await this.apiProvider.auth(this.user);
    if(this.auth.success === true){
      localStorage.setItem('token',this.auth.token);
      localStorage.setItem('name',this.user.name);
      this.navCtrl.push(TabsPage);
    }
    else{
      console.log('TOKEN incorreto');
    }
  }
}



